img {
  width: 450px;
  margin-top: 15px;
}

.breed-select {
  height: 35px;
  min-width: 200px;
  color: #454545;
  text-align: center;
  background-color: #d4fad2;
  border: 2px solid rgba(5, 125, 15, .6);
  border-radius: 10px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}

.loader {
  color: green;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  font-weight: 400;
  display: inline-block;
  position: relative;
}

.loader:before {
  content: "";
  animation: print 2s linear infinite alternate;
}

.loader:after {
  content: "";
  width: 2px;
  height: 1.3em;
  opacity: .8;
  background: currentColor;
  animation: blink 1s steps(2, end) infinite;
  position: absolute;
  top: 50%;
  right: -4px;
  transform: translateY(-45%);
}

@keyframes blink {
  0% {
    visibility: hidden;
  }

  100% {
    visibility: visible;
  }
}

@keyframes print {
  0% {
    content: "L";
  }

  10% {
    content: "Lo";
  }

  20% {
    content: "Loa";
  }

  30% {
    content: "Load";
  }

  40% {
    content: "Loadi";
  }

  50% {
    content: "Loadin";
  }

  60% {
    content: "Loading";
  }

  70% {
    content: "Loading.";
  }

  80% {
    content: "Loading..";
  }

  90%, 100% {
    content: "Loading...";
  }
}

